import React from 'react'
import {Box,Stack,Typography,Button} from '@mui/material'
import HeroBannerImage from '../assets/images/banner.png'

const HeroBanner = () => {
  return (
   <Box sx={{
    mt: {lg: '212px' ,xs:'90px'},
    ml: {sm: '50px'}
   }}>
    <Typography color='blue'
    fontWeight='600' fontSize='26px'>
        GYM NATION
    </Typography>
    <Typography fontWeight={700}
    sx={{ fontSize: {lg:'44px' , xs:"40px"}}}
    mb='23px' mt={'30px'}>
        The body achieves what the <br />mind believes.
    </Typography>
    <Typography fontSize='22px'
    lineHeight='35px' mb='4px'>
        Check out The Most Effecent WorkOut
    </Typography>
    <Button variant='contained' color='error' href='#exercises' sx={{backgroundColor: 'blue',padding:'10px'}}>
        Click Here
    </Button>
    <Typography fontWeight={600}
    color='#ff2625'
    sx={{
        opacity: 0.1,
        display: { lg:'block',xs:'none'}
    }}
    fontSize='200px'
    >
        Fitness
        
    </Typography>
    <img style={{borderRadius:'50px'}} src={HeroBannerImage} alt="banner" className="hero-banner-img " />

   </Box>
  )
}

export default HeroBanner